$primary: map-get($palette-base, primary);

.tutorial {
  h1 {
    color: $primary;
    text-align: left;
    margin: 0 0 10px 15px;
  }
  &__wrapper {
    background-color: $primary ;
    margin: 0 16px;
    padding: 25px 16px 30px 16px;
    border-radius: 5px;
  }

  h3 {
    margin-bottom: 18px;
  }
  p {
    margin-bottom: 15px;
    padding: 0 10px;
  }
}
